
*,
*::after,
*::before, {
    box-sizing: border-box;
}

html {
    color: antiquewhite;
    font-family: 'Montserrat', sans-serif;
}

body {
    margin: 0;
    padding: 0;
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(images/film.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

img {
    max-width: 100%;
}

.container {
    margin: 0 auto;
    max-width: 1000px;
    width: 80%;
}

h1 {
    font-size: 3rem;
    text-align: center;
    margin-top: 100px;
    margin-bottom: 50px;
}

.form {
    display: flex;
    flex-direction: column;
}

label {
    text-transform: uppercase;
}

input {
    margin: 10px 0;
    padding: .4em 1.5em;
    border-radius: 20px;
    border: 1px solid #000;
}

button {
    padding: .4em 2em;
    border-radius: 20px;
    border: 1px solid #000;
    background-color: antiquewhite;
    cursor: pointer;
    transition: 250ms ease-in;
}

button:hover {
    opacity: .8;
    color: black;
}

/*    
        card list     
                        */


.card-list {
    color: #555;
    margin-top: 50px;
}

.card {
    margin: 20px 0;
    background-color: antiquewhite;
    padding: 3em;
    border-radius: 15px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.8)
}

.card--img {
    margin: 0 auto;
    display: block;
    margin-bottom: 40px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.8)
}

@media (min-width: 900px) {
    .form {
        flex-direction: row;
        align-items: center;
    }
    
    input {
        margin: 0 10px;
    }
    
    .card {
        display: flex;
        gap: 20px;
    }
    
    h3 {
        margin-top: 0;
    }
}







